.cardContainer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 15px;
  padding: 10px;
  margin: 10px;
  border-radius: 25px;
  border: cornflowerblue;
  -ms-flex-align: center;
      align-items: center;
}

.cardContainerHeading {
  color: cornflowerblue; 
  text-align: center;
}


